@-webkit-keyframes bounceAlpha {
    0% {opacity: 1; -webkit-transform: translateY(0px) scale(1); transform: translateY(0px) scale(1);}
    25%{opacity: 0; -webkit-transform:translateY(10px) scale(0.9); transform:translateY(10px) scale(0.9);}
    26%{opacity: 0; -webkit-transform:translateY(-10px) scale(0.9); transform:translateY(-10px) scale(0.9);}
    55% {opacity: 1; -webkit-transform: translateY(0px) scale(1); transform: translateY(0px) scale(1);}
  }
  @keyframes bounceAlpha {
    0% {opacity: 1; -webkit-transform: translateY(0px) scale(1); transform: translateY(0px) scale(1);}
    25%{opacity: 0; -webkit-transform:translateY(10px) scale(0.9); transform:translateY(10px) scale(0.9);}
    26%{opacity: 0; -webkit-transform:translateY(-10px) scale(0.9); transform:translateY(-10px) scale(0.9);}
    55% {opacity: 1; -webkit-transform: translateY(0px) scale(1); transform: translateY(0px) scale(1);}
  }
  .bounceAlpha {
    -webkit-animation-name: bounceAlpha;
            animation-name: bounceAlpha;
    -webkit-animation-duration:2s;
            animation-duration:2s;
    -webkit-animation-iteration-count:infinite;
            animation-iteration-count:infinite;
    -webkit-animation-timing-function:linear;
            animation-timing-function:linear;
}

.bounceAlpha2 {
    -webkit-animation-name: bounceAlpha;
            animation-name: bounceAlpha;
    -webkit-animation-duration:2s;
            animation-duration:2s;
    -webkit-animation-delay:1.5s;
            animation-delay:1.5s;
    -webkit-animation-iteration-count:infinite;
            animation-iteration-count:infinite;
    -webkit-animation-timing-function:linear;
            animation-timing-function:linear;
}

