.mainContainerCompation{
    height: 100vh;
    padding: 0px;
    position: relative;
    width: 100vw;
    background-color: #fafafc;
}
.navbarCompation{
    height: 10%;
    background-color: white;
    width: 100vw;
    z-index: 20;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0px;
}
.containerMenuCompation{
    width: 60%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center
}
.rahnamabutton:hover,
.rahnamabutton:focus {
	background-color: #00a680 !important;
	color: white !important;
	border-color: #00a680 !important;
}
.rahnamabutton {
	font-size: 13px!important;
	font-weight: 300;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.38;
	letter-spacing: normal;
	text-align: right;
    color: #00a680 !important;
	background-color: #fff !important;
	transition: 0.3s;
    font-family: "BYekan";
    border-color: #00a680 !important;
    border-radius: 100px!important;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.contentCompation{
    position: absolute;
    top: 10%;
    z-index: 1000;
    width: 100vw;
    height: 90%;
    padding-top: 6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.score{
    width: 58px;
    height: 58px;
    cursor: pointer;
    background-repeat: none;
    background-position: center;
    background-size: 100% 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 30px;
}

.firstLineScore{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    width: 33%

}
.twoLineScore{
    margin-top: 1.2em;
    width: 28%!important;
}
.treeLineScore{
    margin-top: 1.2em;
    width: 23%!important;
}
.fourLineScore
{
    margin-top: 1.2em;
    width: 16%!important;
}
.competitionMenuJustroMain{
    margin-left: unset
}
.buttonScore{
  background-image: -moz-linear-gradient( 120deg, rgb(227,83,37) 0%, rgb(255,138,101) 100%);
  background-image: -webkit-linear-gradient( 120deg, rgb(227,83,37) 0%, rgb(255,138,101) 100%);
  background-image: -ms-linear-gradient( 120deg, rgb(227,83,37) 0%, rgb(255,138,101) 100%);
  box-shadow: 0px 7px 20px 0px rgba(0, 0, 0, 0.3);
  border-radius: 10vw;
  width: 11vw;
  color: white;
  font-size: 12px;
  float: left;
  margin-right: auto;
}
.contentCompationMobile{
    padding: 0px!important;
    top: 0px!important;
    height: 100%;
}
.navbarCompationMobile{
    height: 8%!important;
}
.USerBottonCompation{
    background-color: white;
    border: 1px solid #cfcfcf;
    border-radius: 50%;
    width:10vw;
    height: 10vw;
    float: left;
    margin-right: auto;
}
.containerMenuCompationMobile {
    width: 100%;
    padding: 0px 1rem;
    justify-content: flex-end;
    
}
.firstLineScoreMobil{
    width: 57%!important;
    flex-wrap: wrap;
    margin-top: 2em;
}
.scoreMobile{
    width: 15vw!important;
    height: 15vw;
    margin-bottom: 0.5em;

}
.buttonScoreMobile{
    
    border-radius: 50%;
    width: 15vw;
    height: 15vw;
    border:none;
    /* 
    border-style: solid;
    border-width: 5px;
    border-color: rgba(135, 135, 135,0.5); */
    background-image: -moz-linear-gradient( 120deg, rgb(227,83,37) 0%, rgb(255,138,101) 100%);
  background-image: -webkit-linear-gradient( 120deg, rgb(227,83,37) 0%, rgb(255,138,101) 100%);
  background-image: -ms-linear-gradient( 120deg, rgb(227,83,37) 0%, rgb(255,138,101) 100%);
    
}
.borderButton{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 17vw;
    height: 17vw;
    background-color: rgba(135, 135, 135,0.5);
}